.form {
    display: flex;
    gap: 12px;
    margin-top: 25px;
    margin-left: 20px;
    justify-content: center;
}

.input {
    width: 300px;
    height: 32px;
    font-size: 18px;

}

.button {
    cursor: pointer;
    padding-left: 12px;
    padding-right: 12px;
    background-color: rgb(131, 159, 181);
        border: none;
        font-weight: 500;
        color: white;
        font-weight: 500;
}

.button:hover,
.button:focus {
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    background-color: rgb(125, 156, 180);
}